'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'
import { Textarea } from '@/components/ui/textarea'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

export default function TeacherDashboard() {
  const [user, setUser] = useState<any>(null)
  const [activeCall, setActiveCall] = useState<any>(null)
  const [location, setLocation] = useState('')
  const [description, setDescription] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const router = useRouter()

  useEffect(() => {
    // Check authentication
    const currentUser = localStorage.getItem('currentUser')
    if (!currentUser) {
      router.push('/')
      return
    }

    const userData = JSON.parse(currentUser)
    if (userData.role !== 'teacher') {
      router.push('/')
      return
    }

    setUser(userData)
    checkActiveCall(userData.id)
  }, [router])

  useEffect(() => {
    // Poll for call status updates every 2 seconds
    const interval = setInterval(() => {
      if (user) {
        checkActiveCall(user.id)
      }
    }, 2000)

    return () => clearInterval(interval)
  }, [user])

  const checkActiveCall = (teacherId: string) => {
    const calls = JSON.parse(localStorage.getItem('emergencyCalls') || '[]')
    const activeCall = calls.find((call: any) => 
      call.teacherId === teacherId && 
      (call.status === 'pending' || call.status === 'accepted')
    )
    setActiveCall(activeCall)
  }

  const handleEmergencyCall = async () => {
    if (!location || !description.trim()) {
      setError('Por favor completa todos los campos')
      return
    }

    setLoading(true)
    setError('')

    try {
      const newCall = {
        id: Date.now().toString(),
        teacherId: user.id,
        teacherName: user.name,
        location,
        description: description.trim(),
        status: 'pending',
        createdAt: new Date().toISOString()
      }

      // Get existing calls
      const existingCalls = JSON.parse(localStorage.getItem('emergencyCalls') || '[]')
      
      // Add new call
      const updatedCalls = [...existingCalls, newCall]
      localStorage.setItem('emergencyCalls', JSON.stringify(updatedCalls))

      // Clear form
      setLocation('')
      setDescription('')
      setActiveCall(newCall)

      // Trigger browser notification for inspectors (simulated)
      if ('Notification' in window && Notification.permission === 'granted') {
        new Notification('Nueva Emergencia - Liceo Pullinque', {
          body: `${user.name} necesita ayuda en ${location}`,
          icon: '/liceo-logo.svg'
        })
      }

    } catch (err) {
      setError('Error al enviar la llamada de emergencia')
    } finally {
      setLoading(false)
    }
  }

  const handleLogout = () => {
    localStorage.removeItem('currentUser')
    router.push('/')
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'pending': return 'bg-yellow-100 text-yellow-800'
      case 'accepted': return 'bg-green-100 text-green-800'
      case 'completed': return 'bg-blue-100 text-blue-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'pending': return 'Esperando Inspector'
      case 'accepted': return 'Inspector en Camino'
      case 'completed': return 'Completado'
      default: return status
    }
  }

  if (!user) {
    return <div className="min-h-screen flex items-center justify-center">Cargando...</div>
  }

  return (
    <div className="min-h-screen p-4">
      {/* Header */}
      <div className="max-w-4xl mx-auto mb-6">
        <div className="flex items-center justify-between bg-white rounded-lg shadow-sm p-4">
          <div className="flex items-center space-x-4">
            <Image
              src="/liceo-logo.svg"
              alt="Liceo Pullinque"
              width={40}
              height={48}
              className="h-12 w-auto"
            />
            <div>
              <h1 className="text-xl font-bold text-blue-900">Panel de Profesor</h1>
              <p className="text-blue-700">Bienvenido, {user.name}</p>
            </div>
          </div>
          <Button 
            onClick={handleLogout}
            variant="outline"
            className="border-blue-200 text-blue-700 hover:bg-blue-50"
          >
            Cerrar Sesión
          </Button>
        </div>
      </div>

      <div className="max-w-4xl mx-auto space-y-6">
        {/* Active Call Status */}
        {activeCall && (
          <Card className="border-l-4 border-l-yellow-500">
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                <span>Llamada de Emergencia Activa</span>
                <Badge className={getStatusColor(activeCall.status)}>
                  {getStatusText(activeCall.status)}
                </Badge>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <p><strong>Ubicación:</strong> {activeCall.location}</p>
                <p><strong>Descripción:</strong> {activeCall.description}</p>
                <p><strong>Hora:</strong> {new Date(activeCall.createdAt).toLocaleString('es-CL')}</p>
                {activeCall.assignedInspector && (
                  <p><strong>Inspector asignado:</strong> {activeCall.assignedInspector}</p>
                )}
              </div>
            </CardContent>
          </Card>
        )}

        {/* Emergency Call Form */}
        {!activeCall && (
          <Card>
            <CardHeader>
              <CardTitle className="text-red-600">Llamada de Emergencia</CardTitle>
              <CardDescription>
                Usa este botón solo en caso de emergencia real cuando necesites salir de la sala
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="location">Ubicación</Label>
                <Select onValueChange={setLocation}>
                  <SelectTrigger className="w-full">
                    <SelectValue placeholder="Selecciona tu ubicación actual" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Sala 1-A">Sala 1-A</SelectItem>
                    <SelectItem value="Sala 1-B">Sala 1-B</SelectItem>
                    <SelectItem value="Sala 1-C">Sala 1-C</SelectItem>
                    <SelectItem value="Sala 1-D">Sala 1-D</SelectItem>
                    <SelectItem value="Sala 2-A">Sala 2-A</SelectItem>
                    <SelectItem value="Sala 2-B">Sala 2-B</SelectItem>
                    <SelectItem value="Sala 2-C">Sala 2-C</SelectItem>
                    <SelectItem value="Sala 2-D">Sala 2-D</SelectItem>
                    <SelectItem value="Sala 3-M">Sala 3-M</SelectItem>
                    <SelectItem value="Sala 3-P">Sala 3-P</SelectItem>
                    <SelectItem value="Sala 3-A">Sala 3-A</SelectItem>
                    <SelectItem value="Sala 3-C">Sala 3-C</SelectItem>
                    <SelectItem value="Sala 4-A">Sala 4-A</SelectItem>
                    <SelectItem value="Sala 4-P">Sala 4-P</SelectItem>
                    <SelectItem value="Sala 4-MA">Sala 4-MA</SelectItem>
                    <SelectItem value="Sala 4-MB">Sala 4-MB</SelectItem>
                    <SelectItem value="Sala 4-C">Sala 4-C</SelectItem>
                    <SelectItem value="Laboratorio de Ciencias">Laboratorio de Ciencias</SelectItem>
                    <SelectItem value="Laboratorio de Computación">Laboratorio de Computación</SelectItem>
                    <SelectItem value="Biblioteca">Biblioteca</SelectItem>
                    <SelectItem value="Gimnasio">Gimnasio</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="description">Descripción de la Emergencia</Label>
                <Textarea
                  id="description"
                  placeholder="Describe brevemente la situación (ej: emergencia médica, problema disciplinario, etc.)"
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                  rows={3}
                />
              </div>

              {error && (
                <Alert className="border-red-200 bg-red-50">
                  <AlertDescription className="text-red-700">
                    {error}
                  </AlertDescription>
                </Alert>
              )}

              <Button 
                onClick={handleEmergencyCall}
                disabled={loading || !location || !description.trim()}
                className="w-full bg-red-600 hover:bg-red-700 text-white font-bold py-3"
                size="lg"
              >
                {loading ? 'Enviando...' : '🚨 LLAMAR INSPECTOR'}
              </Button>
            </CardContent>
          </Card>
        )}

        {/* Instructions */}
        <Card>
          <CardHeader>
            <CardTitle>Instrucciones</CardTitle>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2 text-sm text-gray-600">
              <li>• Usa el botón de emergencia solo cuando realmente necesites salir de la sala</li>
              <li>• Selecciona tu ubicación exacta para que el inspector te encuentre rápidamente</li>
              <li>• Describe brevemente la situación para que el inspector sepa qué esperar</li>
              <li>• Una vez enviada la llamada, espera en tu ubicación hasta que llegue el inspector</li>
              <li>• El sistema te notificará cuando un inspector haya aceptado tu llamada</li>
            </ul>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
